.m-welcome {
  height: 100%;
  color: #000;
  display: grid;
  grid-template-areas: 'welcome';
}
.m-welcome__img {
  grid-area: welcome;
}
.m-welcome__inner {
  grid-area: welcome;
  margin-top: 210px;
  margin-bottom: 25px;
}

.m-welcome__content {
  max-width: 780px;
}

.m-welcome__title {
  font-size: clamp(36px, 3.5vw, 48px);
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: clamp(40px, 3.5vw, 80px);

  span {
    color: var(--primary-color);
  }
}
.m-welcome__subtitle {
  font-size: clamp(18px, 3.5vw, 20px);
  font-weight: 700;
  margin-bottom: 10px;
}
.m-welcome__list {
  list-style-type: none;
  padding-left: 10px;
  margin-bottom: clamp(35px, 3.5vw, 80px);
}
.m-welcome__item {
  display: flex;
  align-items: center;
  column-gap: 10px;
  font-size: clamp(18px, 3.5vw, 20px);
  &::before {
    display: block;
    content: '-';
  }
}

.m-welcome__o-btn {
  width: 183px;
}
.m-welcome__row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 30px;
}

@media (max-width: 1199.98px) {
  .m-welcome__inner {
    margin-top: 150px;
  }
}
@media (max-width: 1078.98px) {
  .m-welcome__img {
    picture {
      height: 100%;
      img {
        height: 100%;
      }
    }
  }
}