.menu__list {
  display: flex;
  align-items: center;
  column-gap: 60px;
  list-style: none;
}
.menu__link {
  color: #fff;
  font-size: 13px;
  font-weight: 600;
  text-transform: uppercase;

  &.active {
    color: var(--primary-color);
  }
  &:hover:not(.active) {
    color: var(--primary-color);
    transition: color 0.3s ease;
  }
}

.mobile__btn {
  display: none;
}

.icon-menu {
  width: 35px;
  height: 35px;
  fill: var(--primary-color);

  &:hover,
  &:active {
    fill: var(--primary-color);
    transition: fill 0.3s ease;
  }
}

@media (max-width: 767.98px) {
  .menu {
    & .mobile__btn {
      display: block;
      & .icon-menu--close {
        display: none;
      }
    }

    & .menu__list {
      display: none;
    }
  }
  .menu.mobile {
    & .icon-menu--burger {
      display: none;
    }
    & .icon-menu--close {
      display: block;
    }
    & .mobile__btn {
      position: relative;
      z-index: 99;
    }
    & .menu__list {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      padding: 45px 30px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      background-color: rgba(16, 16, 29, 0.9);
    row-gap: 15px;
    }
    & .menu__link {
      font-size: 34px;
    }
  }
}
