.m-placement {
    color: var(--secondary-color);
    padding-top: 65px;
    padding-bottom: 65px; 
}

.m-placement--bg {
    background-image: url("../assets/images/bg.webp");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
}

.m-placement__title {
font-size: clamp(28px, 3.5vw, 36px);
font-weight: 800;
text-transform: uppercase;
text-align: center;
margin-bottom: 30px;
    span {
        color: var(--primary-color);
    }

}

.m-placement__subtitle {
    color: #bbb;
    font-size: 18px;
    text-align: center;
}

.work-scheme__list {
    list-style-type: none;
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
    column-gap: 30px;
    padding-top: 80px;
    padding-bottom: 80px;
}
.work-scheme__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 0 0 209px;
    row-gap: 20px;
}
.work-scheme__text {
    text-align: center;
}

.work-scheme__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 100px;
    border: 5px solid #286690;
    border-radius: 50%;
    background-color: var(--primary-color);
    position: relative;
}
.icon {
    width: 55px;
    height: 55px;
    fill: var(--secondary-color);
}
.work-scheme__item:not(:last-child) .work-scheme__icon::after {
    position: absolute;
    top: 50%;
    left: 120%;
    content: '';
    background-image: url('../assets/images/dotte.svg');
    width: 110px;
    height: 6px;
}

.select-wrapper{
    position: relative;
  }
  
  .select-wrapper::after {
    content: url('../assets/images/arrow.svg');
    width: 25px;
    height: 20px;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
  }

.select-wrapper select {
    appearance: none;
    width: 100%;
    height: 48px;
    padding: 10px;
    border-color: #fff;
    background: rgba(255, 255, 255, 0.85);
    border-radius: 3px;
    color: #272733;
    font-size: 18px;
  } 

  .input-label {
    display: flex;
    align-items: center;
    width: 100%;
    height: 48px;
    padding: 10px;
    border-color: #fff;
    background: rgba(255, 255, 255, 0.85);
    border-radius: 3px;
  }
  .input-label .input {
    width: 100%;
    color: #272733;
    font-size: 18px;
  }
  .input-label .input::placeholder {
    color: #272733;
    font-size: 18px;
  }

  .input-label-file div{
    border-radius: 3px;
    border: 2px solid #3E9CDC;
    background: #FFF;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 10px;
  }

  .input-label-file .input-title {
    color: #272733;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
  }

  .upload-image {
    user-select: none;
    width: 17px;
    height: 20px;
  }

  .form .input-label-file {
    position: relative;
    z-index: 10;
}

.form input[type=file] {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    position: absolute;
    z-index: -10;
}

.form__btn {
    width: 100%;
    margin-top: 20px;
}
.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

input[type=range]{
-webkit-appearance: none;
width: 100%;
}
input[type=range]::-webkit-slider-runnable-track {
    border-radius: 10px;
    height: 10px;
    border: 1px solid #000;
    background-color: rgba(255, 255, 255, 0.70);
    }

input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    background-color: #42A9ED;
    border: 3px solid #286690;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    margin-top: -12px;
    }

input[type=range]::-moz-range-track {
    border-radius: 10px;
    height: 10px;
    border: 1px solid #000;
    background-color: rgba(255, 255, 255, 0.70);
}

input[type=range]::-moz-range-thumb {
    background-color: #42A9ED;
    border: 3px solid #286690;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    margin-top: -12px;
}

input[type="range"]::-ms-track {
    border-radius: 10px;
    height: 10px;
    border: 1px solid #000;
    background-color: rgba(255, 255, 255, 0.70);
    }

input[type="range"]::-ms-thumb
{
    background-color: #42A9ED;
    border: 3px solid #286690;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
}

.form {
    display: grid;
    grid-template-areas:"select email name"
                        "range range file"
                        ". submit .";
    gap: 30px;
}
.select-wrapper {
    grid-area: select;
}
.input-label-email {
    grid-area: email;
}
.input-label-name {
    grid-area: name;
}
.input-label-range {
    grid-area: range;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.input-label-file {
    grid-area: file;
}
.form__btn {
    grid-area: submit;
}


@media (max-width: 1199.98px) {
    .work-scheme__item {
        flex: 0 1 209px;
      }
  }


@media (max-width: 991.98px) {
    .work-scheme__item:not(:last-child) .work-scheme__icon::after {
       display: none;
      }
      .work-scheme__list {
        flex-wrap: wrap;
        row-gap: 30px;
      }

      .form {
        grid-template-areas:
      "select select select"
      "email  name file" 
      "range  range  range" 
      ".      submit .   ";
      }
  }

  @media (max-width: 767.98px) {
    .form {
        grid-template-areas:
      "select select"
      "email  name" 
      "range  range"
      "file   submit";
      }
      .form__btn {
        margin-top: 0;
        height: 48px;
      }
  }
  
  @media (max-width: 575.98px) {
    .form {
        grid-template-areas:
      "select"
      "email"
      "name"
      "range"
      "file"
      "submit";
      }
      .form__btn {
        margin-top: 20px;
        height: auto;
      }
  }
 