.logo__link {
color: var(--secondary-color);
font-family: "Raleway", sans-serif;
font-size: clamp(22px, 3.5vw, 28px);
font-weight: 800;
text-transform: uppercase;
transition: all 0.3s ease;
	span {
		color: var(--primary-color);
	}
}
.logo__link:hover {
	color: var(--primary-color);
	span {
		color: var(--secondary-color);
	}
}