/*
=== RESET STYLES ===
*/

/* Reset and base styles  */
* {
  padding: 0;
  margin: 0;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue',
    'Noto Sans', 'Liberation Sans', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-optical-sizing: auto;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  margin: 0;
}

/* Links */
a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
}

a:not([class]):link {
  color: #007bff;
  text-decoration: none;
}

a:not([class]):visited {
  color: #666;
}

a:not([class]):hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([class]):active {
  color: #ff0000;
}

/* Common */

aside,
nav,
footer,
header,
section,
main {
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-size: inherit;
  font-weight: inherit;
}

ul:not([class]),
ol:not([class]) {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  width: 100%;
  object-fit: cover;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

picture {
  display: inline-block;
  width: 100%;
  vertical-align: bottom;
}

address {
  font-style: normal;
}

/* Form */

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button,
input[type='submit'] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}
/* Castom */
a,
button,
input,
select,
textarea {
  outline: 0 !important;
}

button {
  display: inline-block;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: normal;
  text-align: center;
  border: 0;
  cursor: pointer;
}
