.m-footer {
  padding-top: 30px;
  padding-bottom: 30px;
  background-color: #3d4050;
  font-size: 13px;

  ul {
    list-style-type: none;
    display: flex;
    align-items: center;
    column-gap: 38px;
  }
}

.m-footer__row > ul::before {
  display: block;
  content: '|';
  color: #c3c3c3;
}

.m-footer__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.m-footer li, .m-footer li a {
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.payment__item {
  color: #c3c3c3;
}

.payment__icon,
.contacts__icon {
  width: 20px;
  height: 20px;
  fill: var(--primary-color);
}

.contacts__link {
  color: #FFF;
}


.copyright {
  color: #FFF;
  span {
    text-transform: uppercase;
  }
}

.m-footer svg {
  flex: 0 0 auto;
}

@media (max-width: 1199.98px) {
  .m-footer__row > ul::before {
    display: none;
  }
  .m-footer__row {
    flex-wrap: wrap-reverse;
    row-gap: 25px;
  }
}

@media (max-width: 575.98px) {
  .m-footer ul {
    column-gap: 15px;
  }
}